<template>
  <vx-card no-shadow>

    <vs-input type="password" class="w-full mb-base" label-placeholder="Mot de passe" v-model="new_password.old_password" />
    <vs-input type="password" class="w-full mb-base" label-placeholder="Nouveau mot de passe" v-model="new_password.new_password1" />
    <vs-input type="password" class="w-full mb-base" label-placeholder="Confirmation mot de passe" v-model="new_password.new_password2" />

    <!-- Save & Reset Button -->
    <div class="flex flex-wrap items-center justify-end">
      <vs-button class="ml-auto mt-4" @click="ChangeUserPassword">Sauvegarder les modifications</vs-button>
    </div>
  </vx-card>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      new_password: {
        old_password: "",
        new_password1: "",
        new_password2: "",
      }

    }
  },
  computed: {
    activeUserInfo() {
      return this.$store.state.AppActiveUser
    },
  },
  methods: {
    async ChangeUserPassword() {
      await axios
        .post("/authentification/password/change/", this.new_password, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Modification avec succès",
            color: "success",
          });
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur de modification",
            text: "veuillez saisir un mot de passe différent",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
        })
    },
  },
}
</script>
