<template>
  <vx-card no-shadow>

    <!-- Img Row -->
    <div class="flex flex-wrap items-center mb-base">
      <vs-avatar :src="activeUserInfo.photoURL" size="70px" class="mr-4 mb-4" />
      <div>
        <vs-button class="mr-4 sm:mb-0 mb-2">Upload photo</vs-button>
        <vs-button type="border" color="danger">Remove</vs-button>
        <p class="text-sm mt-2">Allowed JPG, GIF or PNG. Max size of 800kB</p>
      </div>
    </div>

    <!-- Info -->
    <vs-input class="w-full mb-base" label-placeholder="Nom d'utilisateur" v-model="currentUser.username"></vs-input>
    <vs-input class="w-full mb-base" label-placeholder="Nom" v-model="currentUser.first_name"></vs-input>
    <vs-input class="w-full mb-base" label-placeholder="Prénom" v-model="currentUser.last_name"></vs-input>
    <vs-input class="w-full" label-placeholder="Email" v-model="currentUser.email"></vs-input>

    <!-- Save & Reset Button -->
    <div class="flex flex-wrap items-center justify-end">
      <vs-button class="ml-auto mt-4" @click="ChangeUserInfo">Sauvegarder les modifications</vs-button>
    </div>
  </vx-card>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      username: "johny_01",
      name: this.$store.state.AppActiveUser.displayName,
      email: "john@admin.com",
      company: "SnowMash Technologies Pvt Ltd",
      currentUser: {}
    }
  },
  computed: {
    activeUserInfo() {
      return this.$store.state.AppActiveUser
    },
  },
  mounted() {
    this.GetCurrentUser()
  },
  methods: {
    async ChangeUserInfo() {
      await axios
        .put("/authentification/user/",this.currentUser ,{
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Modification avec succès",
            color: "success",
          });
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur de modification",
            text: "veuillez vérifier vos informations",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
        })
    },
    async GetCurrentUser() {
      await axios
        .get("/authentification/user/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.currentUser = res.data;
        })
    },
  },
}
</script>
